@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.page {
  box-sizing: border-box;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  overflow-x: hidden;
}

.navigation {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-content: flex-start;
}

.header {
  margin: 30px 0 0 40px;
  padding: 0;
  position: absolute;
  font-size: 42px;
  text-transform: uppercase;
  font-weight: 900;
  line-height: 42px;
  font-family: "Playfair Display", serif;
  letter-spacing: 3px;
}

.header span {
  font-size: 20px;
  color: #727171;
  letter-spacing: 1px;
}

.tabs {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.tab-list {
  display: flex;
  position: absolute;
  list-style: none;
  justify-content: space-evenly;
  margin: 0;
  padding: 0;
  width: 35rem;
  align-self: flex-end;
}

.tab-list-item {
  padding: 42px 0 6px 0;
  font-size: 17px;
  text-transform: uppercase;
  font-family: "Work Sans", sans-serif;
  font-weight: 400;
  letter-spacing: 2px;
  color: #141414;
  cursor: pointer;
}

.tab-list-active {
  border-bottom: 1px solid black;
}

.tab-content {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.icons {
  width: 35rem;
  display: flex;
  margin: 0 0 8px 0;
  padding: 0 5rem;
  box-sizing: border-box;
  justify-content: space-evenly;
  float: right;
  cursor: pointer;
}


/* -------------------  Home Page ------------------- */

.home-image, 
.current-photo, 
.about-image, 
.contact-image {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100%;
  width: calc(100% - 35rem);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1000;
  box-sizing: border-box;
}

.home-image {
  background-image: url(../public/media/jan-kopriva-4ENFPMd4hUU-unsplash.jpg);
}

.text, 
.about-text {
  width: auto;
  margin: 9% auto 0 50%;
}

.first {
  font-family: "Work Sans", sans-serif;
  font-size: 48px;
  font-weight: 100;
  line-height: 48px;
  letter-spacing: 3px;
  margin: 0;
  padding: 0;
  color: #727171;
}

.name, 
.contact-us {
  font-family: "Playfair Display", serif;
  font-size: 106px;
  line-height: 106px;
  margin: 0;
  padding: 0;
  width: 300px;
  font-weight: 900;
  letter-spacing: 8px;
}

.profession, 
.message {
  font-family: "Work Sans", sans-serif;
  font-size: 25px;
  font-weight: 200;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  color: #727171;
  margin: 10px 0 40px 0;
  line-height: 25px;
}

.description, 
.contact-info {
  font-family: "Work Sans", sans-serif;
  font-size: 18px;
  font-weight: 200;
  letter-spacing: 1px;
  color: #43474D;
  margin: 0;
  line-height: 30px;
  max-width: 690px;
}


/* -------------------  Portfolio Page ------------------- */

.gallery {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.current-photo {
  background-image: url(../public/media/fabrice-villard-Jrl_UQcZqOc-unsplash.jpg);
}

.photo-all {
  width: 25rem;
  height: 75%;
  position: absolute;
  right: 4.5rem;
  top: 14%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-gap: 15px;
  overflow-y: scroll;
  box-sizing: border-box;
}

.photo-all img {
  height: 232px;
  width: 173px;
  cursor: pointer;
}

.photo-all::-webkit-scrollbar {
  width: 8px;
}

.photo-all::-webkit-scrollbar-thumb {
  background-color: rgb(202, 202, 202);
  border-radius: 10px;
}


/* -------------------  About Page ------------------- */

.about-image {
  background-image: url(../public/media/unsplash_mXu1SpzHq6w.png);
}

.about-text .name {
  padding-left: 2.3rem;
}

.about-text .description,
.contact-info {
  width: 25rem;
  position: absolute;
  right: 5rem;
  padding-top: 2.5rem;
}

/* -------------------  Contact Page ------------------- */

.contact-image {
  background-image: url(../public/media/adam-birkett-lkNa1eg2d18-unsplash.jpg);
}

.contact {
  margin-left: 32.3%;
  box-sizing: border-box;
}

.contact-text {
  box-sizing: border-box;
}

.contact-us {
  width: auto;
  padding-bottom: 10px;
}

.contact-info {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 3fr 1fr;
  height: 13rem;
  box-sizing: border-box;
}


/* -------------------  Media Queries ------------------- */

@media only screen and (max-width: 1100px) {
  .text,
  .about-text {
    margin: 10% auto 0 30%;
  }

  .contact {
    width: 600px;
  }
}

@media only screen and (max-height: 760px) {
  .page,
  .navigation {
    min-height: 700px;
    height: 100vh !important;
    overflow-y: scroll;
  }

  .home-image, 
  .current-photo, 
  .about-image, 
  .contact-image {
    height: 100vh !important;
    min-height: 700px;
  }

  .photo-all {
    height: 550px;
    top: 130px;
  }
}

@media only screen and (max-width: 920px) {
  
  .home-image, 
  .current-photo, 
  .about-image, 
  .contact-image {
    width: 100%;
    height: 100%;
  }

  .header {
    font-size: 38px;
    letter-spacing: 2.5px;
  }

  .tab-list {
    width: 29rem;
  }

  .tab-list-item {
    font-size: 15px;
    padding: 44px 0 6px 0;
  }

  .footer {
    text-align: center;
    height: fit-content;
    box-sizing: border-box;
  }

  .icons {
    float: none;
    margin: 8px auto 8px auto;
    box-sizing: border-box;
  }

  .text,
  .about-text {
    width: 75%;
    margin: 18% auto 0 auto;
  }

  .first {
    font-size: 40px;
    line-height: 40px;
  }

  .name, 
  .contact-us {
    font-size: 86px;
    letter-spacing: 6px;
    line-height: 90px;
  }

  .profession, 
  .message {
    font-size: 20px;
    letter-spacing: 2px;
  }

  .description,
  .contact-info {
    font-size: 16px;
    line-height: 25px;
  }

  .about-text .description,
  .contact-info {
    width: 80%;
    margin: 35px auto 0 auto;
    position: unset;
    right: unset;
    padding: 20px;
    background-color: #0000006e;
    color: rgb(223, 223, 223);
    border-radius: 10px;
  }

  .bothersome div {
    box-sizing: border-box;
  }

  .contact {
    max-width: 400px !important;
    margin-top: 23%;
  }

  .contact-info {
    height: 200px;
    margin: 0;
    width: 90%;
  }

  .contact .message {
    width: 500px;
  }

  .current-photo {
    display: none;
  }

  .gallery {
    width: 100%;
    height: 100%;
  }

  .photo-all {
    grid-template-columns: none;
    grid-template-rows: auto;
    height: 83%;
    width: 90%;
    margin: 12.5% 5% 0 5%;
    padding: 0 20px;
    position: unset;
    right: unset;
    top: unset;
    grid-gap: 25px;
  }

  .photo-all img {
    width: 100%;
    height: auto;
  }

}

@media only screen and (max-width: 650px) {
  .navigation {
    flex-direction: column;
    justify-content: unset;
    align-content: center;
  }

  .header,
  .tab-list {
    position: unset;
    align-self: center;
    margin: 20px 0 0 0;
  }

  .tab-list-item {
    padding: 0;
  }

  .text, 
  .about-text {
    margin: 7% auto;
  }

  .contact, 
  .about-text {
    text-align: center;
  }

  .contact-us, 
  .name {
    font-size: 70px;
    letter-spacing: 3.5px;
    line-height: 73px;
    width: auto;
    padding: 0;
  }

  .contact .message {
    margin: 5px 0 40px 0;
    width: auto;
    font-weight: 300;
    font-size: 18px;
  }

  .contact-info {
    width: 100%;
  }

  .tab-content .div {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }

  .icons {
    width: 100%;
    padding: 0 auto;
  }

  .icons img {
    padding: 0 20px;
  }

  .first {
    font-size: 30px;
    letter-spacing: 2.5px;
  }

  .about-text .name {
    font-size: 70px;
    padding: unset;
  }

}

@media only screen and (max-width: 550px) {
  .tab-list {
    width: 100%;
    flex-wrap: wrap;
  }

  .tab-list-item {
    margin: 0px 15px 20px 15px;
  }

  .about-text,
  .text,
  .contact {
    width: 87%;
    margin: 4% auto 0 auto;
    box-sizing: border-box;
  }

  .first {
    font-size: 26px;
    line-height: 32px;
    letter-spacing: 2px;
  }

  .name, 
  .about-text .name,
  .contact-us {
    font-size: 65px;
    letter-spacing: 3px;
  }

  .gallery {
    height: max-content;
  }

  .description {
    font-size: 15px;
  }

}